  font-size: 16px;
  line-height: 21px;
  box-sizing: border-box;
  border: none;
  border-radius: 3px;
  background: rgba(4, 20, 39, 0.15);
  color: #ffffff;
  padding: 14px 16px 15px; }

.form__question {
  padding: 25px 15px;
  resize: none;
  min-height: 155px; }
  @media (max-width: 1023px) {
    .form__question {
      padding: 30px 15px; } }

.form__item input::placeholder,
.form__question::placeholder {
  color: #ffffff; }

.form__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  align-items: center; }
  @media (max-width: 1279px) {
    .form__wrapper {
      gap: calc(50 / 1280 * 100vw); } }
  @media (max-width: 1023px) {
    .form__wrapper {
      grid-template-columns: 265px 1fr;
      gap: 85px; } }

.form__wrapper button {
  background: #041427;
  cursor: pointer;
  border: none;
  border-radius: 3px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 19px;
  font-weight: 700;
  color: #ffffff;
  padding: 15px 25px 11px;
  box-sizing: border-box;
  letter-spacing: 0.7px; }

.control {
  position: relative;
  cursor: pointer;
  padding-left: 26px;
  margin-top: 15px; }
  @media (max-width: 1023px) {
    .control {
      margin-top: 6px; } }

.control__mark-icon {
  display: none;
  position: absolute;
  top: 11px;
  left: 3px;
  width: 9px;
  height: 6px;
  content: "";
  z-index: 2;
  fill: transparent; }

.control__mark {
  position: absolute;
  top: 7px;
  left: 0;
  width: 13px;
  height: 13px;
  border-radius: 2px;
  border: 1px solid #ffffff;
  content: ""; }

.control span:last-child {
  font-size: 14px;
  line-height: 15px;
  display: block; }

.control input[type="checkbox"]:checked + .control__mark-icon {
  display: block; }

.modal {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(3, 16, 27, 0.7);
  z-index: 1000;
  display: block;
  color: #ffffff; }

.modal__wrapper {
  width: 500px;
  padding: 36px 40px 40px;
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1001;
  background: #00b3ff;
  border-radius: 3px;
  min-height: 610px; }

.modal__window h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;
  margin: 0 0 6px; }

.modal__window p {
  margin: 0 0 40px; }

.modal__button {
  width: 70px;
  height: 70px;
  border: none;
  background: transparent;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer; }

.modal__button-icon {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto; }

