.nav {
  width: 1220px;
  margin: 0 auto;

  @media (max-width: $desktop-stretching-width) {
    width: calc(1220 / 1280 * 100vw);
  }

  @media (max-width: $max-tablet-width) {
    width: 670px;
  }

  @media (max-width: $max-tablet-width-control-point) {
    width: calc(670 / 770 * 100vw);
  }
}

.nav__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 60px;
  min-height: 110px;

  @media (max-width: $max-tablet-width) {
    min-height: 90px;
    margin-right: 0;
  }
}

.nav__link {
  display: grid;
  grid-template-columns: 55px 1fr;
  width: 215px;
  gap: 15px;
  text-decoration: none;
  color: $light-color-type-2;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  align-items: center;
}

.nav__link--active {
  pointer-events: none;
}

.nav__link span {
  display: block;
  margin-top: 2px;
}

.nav__link .nav__link-description {
  color: $light-color-type-3;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  margin: 0;
}

.nav__button {
  background: transparent;
  color: $light-color-type-2;
  border: 1px solid $light-color-type-2;
  border-radius: 3px;
  text-transform: uppercase;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 700;
  padding: 10px 17px 11px;
  cursor: pointer;
  letter-spacing: 0.6px;

  @media (max-width: $max-tablet-width) {
    display: none;
  }
}
